import React, { useContext } from "react"
import { GlobalContext } from '../context/GlobalState'
import { motion } from "framer-motion"

import vueIcon from "../images/vue.png"
import reactIcon from "../images/react.png"
import swiftIcon from "../images/swift.png"
import djangoIcon from "../images/django.png"
import railsIcon from "../images/rails.png"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'

const Frameworks = props => {
  const { onDarkMode } = useContext(GlobalContext);
  const modeClasses = onDarkMode ? "dark-mode-background dark-mode-outline" : "light-mode-background light-mode-outline";
  
  const frameworks = [
    {
      link: "https://rubyonrails.org/",
      image: railsIcon,
      name: "Rails",
      color: "limegreen",
      animationClass: "flash-green"
    },
    {
      link: "https://www.djangoproject.com/",
      image: djangoIcon,
      name: "Django",
      color: "darkorange",
      animationClass: "flash-orange"
    },
    {
      link: "https://vuejs.org/",
      image: vueIcon,
      name: "Vue.js",
      color: "darkgreen",
      animationClass: "flash-darkgreen"
    },
    {
      link: "https://reactjs.org/",
      image: reactIcon,
      name: "React.js",
      color: "limegreen",
      animationClass: "flash-green"
    },
    {
      link: "https://developer.apple.com/xcode/swiftui/",
      image: swiftIcon,
      name: "SwiftUI",
      color: "orange",
      animationClass: "flash-orange"
    },
  ];

  return (
    <div className={`frameworks-container ${modeClasses}`}>
      <style jsx>{`
        @keyframes flash-green {
          0% { color: limegreen; }
          50% { color: #a0ffa0; }
          100% { color: limegreen; }
        }
        @keyframes flash-orange {
          0% { color: darkorange; }
          50% { color: #ffb366; }
          100% { color: darkorange; }
        }
        @keyframes flash-darkgreen {
          0% { color: darkgreen; }
          50% { color: #00a000; }
          100% { color: darkgreen; }
        }
        .flash-green {
          animation: flash-green 2s infinite;
        }
        .flash-orange {
          animation: flash-orange 2.3s infinite;
        }
        .flash-darkgreen {
          animation: flash-darkgreen 1.8s infinite;
        }
      `}</style>
      <table>
        <tbody>
          {frameworks.map((framework, index) => {
            return (
              <motion.tr 
                key={index}
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ 
                  delay: 0.3 + (index * 0.1), 
                  duration: 0.5,
                  type: "spring",
                  stiffness: 100
                }}
              >
                <td key={index}>
                  <div>
                    <motion.span
                      whileHover={{ scale: 1.2 }}
                      transition={{ duration: 0.2 }}
                    >
                      <FontAwesomeIcon
                        icon={faCircle}
                        size="sm"
                        className={framework.animationClass}
                      />
                    </motion.span>
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    <span className="framework-name"><b>{framework.name}</b></span>
                  </div>
                </td>
                <td align="center">
                  <motion.a 
                    href={framework.link} 
                    target="_blank" 
                    rel="noopener noreferrer" 
                    className="image"
                    whileHover={{ scale: 1.15, rotate: 5 }}
                    whileTap={{ scale: 0.9 }}
                    transition={{ duration: 0.2 }}
                  >
                    <img className="responsive-img logo" src={framework.image} alt={framework.name} />
                  </motion.a>
                </td>
              </motion.tr>
            )
          })}
        </tbody>
      </table>
    </div>
  );
}

export default Frameworks;
