import React, { useContext } from "react"
import { GlobalContext } from '../context/GlobalState'
import { motion } from "framer-motion"
import "../styles/navbar.scss"
import "../styles/animations.scss"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const NavBar = props =>  {
  const { onDarkMode } = useContext(GlobalContext);
  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";

  return (
    <motion.div 
      className="nav-bar"
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <motion.h5 
        className="nav-title"
        whileHover={{ scale: 1.05 }}
        transition={{ duration: 0.3 }}
      >
        Calvin <b>Lui</b>
      </motion.h5>

      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.3, duration: 0.5 }}
      >
        <motion.a 
          href="https://www.linkedin.com/in/calvinhklui/" 
          target="_blank" 
          rel="noopener noreferrer" 
          className="image"
          whileHover={{ scale: 1.1, rotate: 5 }}
          whileTap={{ scale: 0.95 }}
          transition={{ duration: 0.2 }}
        >
          <FontAwesomeIcon icon={['fab', 'linkedin']} size="2x" className={`icon ${modeClasses}`} />
        </motion.a>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <motion.a 
          href="https://github.com/calvinhklui" 
          target="_blank" 
          rel="noopener noreferrer" 
          className="image"
          whileHover={{ scale: 1.1, rotate: -5 }}
          whileTap={{ scale: 0.95 }}
          transition={{ duration: 0.2 }}
        >
          <FontAwesomeIcon icon={['fab', 'github-square']} size="2x" className={`icon ${modeClasses}`} />
        </motion.a>
      </motion.div>
    </motion.div>
  );
}

export default NavBar;
