import React, { useContext, useState, useEffect } from "react";
import { GlobalContext } from '../context/GlobalState'
import "../styles/overview.scss"
import $ from 'jquery';
import Typed from 'typed.js';
import { motion } from "framer-motion";
import AnimatedButton from "./common/animatedButton";
import useWindowSize from "../hooks/useWindowSize";

import Frameworks from "../components/frameworks.js"

const Overview = props => {
  const { onDarkMode } = useContext(GlobalContext);
  const [copied, setCopied] = useState(false);
  const windowSize = useWindowSize();

  const modeClasses = onDarkMode ? "dark-mode-outline" : "light-mode-outline";

  useEffect(() => {
    const options = {
      strings: [
        "Product Builder^1000"
      ],
      typeSpeed: 50,
      backSpeed: 50,
      showCursor: true,
      startDelay: 500,
      onComplete: () => { $(".typed-cursor").hide(); }
    };

    const typed = new Typed('#title-text', options);

    return () => {
      typed.destroy();
    }
  }, []);  const handleClick = (e) => {
    var temp = document.createElement("input");
    document.body.appendChild(temp);
    temp.value = "developer@calvinhklui.com";
    temp.select();
    document.execCommand("copy");
    document.body.removeChild(temp);

    setCopied(true);
    
    // Reset copied state after 3 seconds
    setTimeout(() => {
      setCopied(false);
    }, 3000);
  }
  return (
    <motion.div 
      className="overview-container"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.7 }}
    >
      <div className="introduction-container">
        <div className="words-container">
          <motion.div 
            className="title-container"
            initial={{ y: 20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.3, duration: 0.5 }}
          >
            <h1 className="title"><span id="title-text" /></h1>
          </motion.div>

          <motion.div 
            className="subtitle-container"
            initial={{ y: 20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.5, duration: 0.5 }}
          >
            <h5 className="subtitle">
              Hello there! I'm Calvin. I enjoy working with others to <b>design</b>, <b>build</b>, and <b>ship</b> software products.
            </h5>
          </motion.div>
        </div>        <motion.div 
          className="contact-container"
          initial={{ y: 20, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.7, duration: 0.5 }}
        >
          <AnimatedButton
            className={`contact-button ${modeClasses}`}
            onClick={handleClick}
            id="contactButton"
            value="hello"
          >
            <b>Contact</b>
          </AnimatedButton>
          <span className={copied ? "contact-confirmation copied" : "contact-confirmation"}>
            <b>developer@calvinhklui.com</b> copied to clipboard!
          </span>
        </motion.div>
      </div>

      <motion.div
        initial={{ opacity: 0, x: 20 }}
        animate={{ opacity: 1, x: 0 }}
        transition={{ delay: 0.9, duration: 0.5 }}
      >
        <Frameworks />
      </motion.div>
    </motion.div>
  );
}

export default Overview;
