import React from "react"
import "../styles/projects.scss"
import { motion } from "framer-motion"

import Project from "../components/project.js"

import fitneticImage from "../images/fitnetic.png"
import chessCampImage from "../images/chessCamp.png"
import passkeyDemoImage from "../images/passkeyDemo.png"
import clubsImage from "../images/cmuClubs.png"

const Projects = props => {
  const projects = [
    {
      id: "project-left",
      name: "Fitnetic",
      link: "https://github.com/calvinhklui/Fitnetic",
      image: fitneticImage
    },
    {
      id: "project-center",
      name: "What's a Passkey?",
      link: "/passkeys",
      image: passkeyDemoImage
    },
    {
      id: "project-right",
      name: "CMU Clubs",
      link: "https://github.com/calvinhklui/CMUClubs",
      image: clubsImage
    },
  ];
  return (
    <motion.div 
      className="projects"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5, delay: 0.3 }}
    >
      {projects.map((project, index) => {
        return (
          <motion.div 
            id={project.id} 
            key={index}
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ 
              duration: 0.6, 
              delay: 0.5 + (index * 0.2),
              type: "spring",
              stiffness: 100
            }}
            whileHover={{ 
              scale: 1.03, 
              transition: { duration: 0.3 }
            }}
          >
            <Project
              projectName={project.name}
              projectURL={project.link}
              projectImage={project.image}
            />
          </motion.div>
        )
      })}
    </motion.div>
  );
}

export default Projects;
